.scroll-wrapper {
  overflow: hidden !important;
  padding: 0 !important;
  position: relative;
}
.scroll-wrapper > .scroll-content {
  border: none !important;
  box-sizing: content-box !important;
  height: auto;
  left: 0;
  margin: 0;
  max-height: none;
  max-width: none !important;
  overflow-y: scroll !important;
  padding: 0;
  position: relative !important;
  top: 0;
  width: auto !important;
}
.scroll-wrapper > .scroll-content::-webkit-scrollbar {
  height: 0;
  width: 0;
}
.scroll-element {
  display: none;
}
.scroll-element,
.scroll-element div {
  box-sizing: content-box;
}
.scroll-element.scroll-x.scroll-scrollx_visible,
.scroll-element.scroll-y.scroll-scrolly_visible {
  display: block;
}
.scroll-element .scroll-bar,
.scroll-element .scroll-arrow {
  cursor: default;
}
.scroll-textarea {
  border: 1px solid #cccccc;
  border-top-color: #999999;
}
.scroll-textarea > .scroll-content {
  overflow: hidden !important;
}
.scroll-textarea > .scroll-content > textarea {
  border: none !important;
  box-sizing: border-box;
  height: 100% !important;
  margin: 0;
  max-height: none !important;
  max-width: none !important;
  overflow: scroll !important;
  outline: none;
  padding: 2px;
  position: relative !important;
  top: 0;
  width: 100% !important;
}
.scroll-textarea > .scroll-content > textarea::-webkit-scrollbar {
  height: 0;
  width: 0;
}
.scroll-inner-content {
  height: 100%;
  max-height: 100%;
  overflow: auto;
}
.scroll-inner-content > .scroll-element {
  /* update scrollbar offset if both scrolls are visible */
}
.scroll-inner-content > .scroll-element,
.scroll-inner-content > .scroll-element div {
  border: none;
  margin: 0;
  padding: 0;
  position: absolute;
  z-index: 10;
}
.scroll-inner-content > .scroll-element div {
  display: block;
  height: 100%;
  left: 0;
  top: 0;
  width: 100%;
}
.scroll-inner-content > .scroll-element.scroll-x {
  bottom: 1px;
  height: 8px;
  left: 0;
  width: 100%;
}
.scroll-inner-content > .scroll-element.scroll-y {
  height: 100%;
  right: 1px;
  top: 0;
  width: 8px;
}
.scroll-inner-content > .scroll-element .scroll-element_outer {
  overflow: hidden;
}
.scroll-inner-content > .scroll-element .scroll-element_track {
  background-color: #fff;
}
.scroll-inner-content > .scroll-element .scroll-bar {
  background-color: #aaa;
}
.scroll-inner-content > .scroll-element.scroll-x.scroll-scrolly_visible .scroll-element_track {
  left: -12px;
}
.scroll-inner-content > .scroll-element.scroll-x.scroll-scrolly_visible .scroll-element_size {
  left: -12px;
}
.scroll-inner-content > .scroll-element.scroll-y.scroll-scrollx_visible .scroll-element_track {
  top: -12px;
}
.scroll-inner-content > .scroll-element.scroll-y.scroll-scrollx_visible .scroll-element_size {
  top: -12px;
}
.scroll-inner-content .scroll-element_track,
.scroll-inner-content .scroll-bar {
  border-radius: 8px;
  opacity: 0.01;
  transition: opacity 0.15s;
}
.scroll-inner-content:hover .scroll-element_track {
  opacity: 0.2 !important;
}
.scroll-inner-content:hover .scroll-bar {
  opacity: 0.9 !important;
